<template>
   <div class="BoxArea">
      <div v-if="!myData">
         <content-loader
            :animate="true"
            :height="500"
            :width="350"
            :speed="2"
          primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
            v-if="ResizeScreen == 1"
            style="margin-top:80px;"
         >
            <rect x="10" y="0" rx="0" ry="0" width="330" height="200" />
            <rect x="35" y="220" rx="0" ry="0" width="280" height="20" />
            <rect x="55" y="250" rx="0" ry="0" width="230" height="10" />
            <rect x="55" y="270" rx="0" ry="0" width="230" height="10" />
            <rect x="10" y="300" rx="0" ry="0" width="330" height="100" />
            <rect x="10" y="420" rx="0" ry="0" width="330" height="100" />

         </content-loader>

         <content-loader
            :animate="true"
            :height="600"
            :width="600"
            :speed="2"
          primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
            v-if="ResizeScreen == 2"
            style="margin-top:80px;"
         >
            <rect x="10" y="0" rx="0" ry="0" width="200" height="200" />
            <rect x="35" y="220" rx="0" ry="0" width="150" height="20" />
            <rect x="55" y="250" rx="0" ry="0" width="110" height="10" />
            <rect x="55" y="270" rx="0" ry="0" width="110" height="10" />
            <rect x="10" y="290" rx="0" ry="0" width="200" height="200" />
            <rect x="10" y="500" rx="0" ry="0" width="200" height="100" />

            <rect x="220" y="0" rx="0" ry="0" width="365" height="600" />

         </content-loader>

         <content-loader
            :animate="true"
            :height="290"
            :width="600"
            :speed="2"
          primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
            v-if="ResizeScreen == 3"
         >
            <rect x="0" y="0" rx="0" ry="0" width="150" height="130" />
            <rect x="23" y="140" rx="0" ry="0" width="100" height="15" />
            <rect x="32" y="160" rx="0" ry="0" width="80" height="15" />
            <rect x="15" y="190" rx="0" ry="0" width="50" height="20" />
            <rect x="80" y="190" rx="0" ry="0" width="50" height="20" />
            <rect x="0" y="220" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="230" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="240" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="250" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="260" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="270" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="280" rx="0" ry="0" width="150" height="5" />
            <rect x="0" y="290" rx="0" ry="0" width="150" height="5" />

           <rect x="180" y="0" rx="0" ry="0" width="450" height="190" />
           <rect x="180" y="220" rx="0" ry="0" width="450" height="75" />
         </content-loader>
      </div>

      <div class="BXviewcoach" v-if="myData">
         <div class="BXviewcoach__BXcard" >
            <CardProfile
               :GET_AdditionaLinfo="GET_AdditionaLinfo"
            />
         </div>
         <div class="BXviewcoach__BXInfoStudent">
           <CoachNote
            :DataSession="DataSession"
            v-if="GET_SessionHistory !== null"
           />
           <StudentProgress />
         </div>
      </div>
   </div>

</template>

<script>
   import moment from 'moment'
   import { ContentLoader } from "vue-content-loader"
   import CardProfile from '../../student-components/profile/view-profile/cardProfile/CardProfile.vue';
   import CoachNote from '../../student-components/profile/view-profile/coachNote/CoachNote.vue';
   import StudentProgress from '../../student-components/profile/view-profile/studentProgress/StudentProgress.vue';
   export default {
      components:{
         CardProfile,
         CoachNote,
         StudentProgress,
         ContentLoader
      },
      data() { 
         return {
            myData :null,
            drawer: null,
            windowWidth:0,
            windowHeigt:0,
            ResizeScreen: null
         }
      },
      created() {
         this.TrigDispatch()
      },
      mounted () {

         this.$nextTick( () => {
            window.addEventListener('resize', this.getWindowWidth);
            window.addEventListener('resize', this.getWindowHeight);
            this.getWindowWidth()
            this.getWindowHeight()

            setTimeout(() => {
               this.CheckLoad
            }, 2000);
         })

        
      },
      methods: {
         getWindowWidth(event){
            this.windowWidth = document.documentElement.clientWidth;

            if (this.windowWidth <= 599){
            //this for resolution phone only
            this.ResizeScreen = 1
            }else if(this.windowWidth <= 768){
               //this for resolution Ipad Potrait
            this.ResizeScreen = 2
            }else if(this.windowWidth >= 1024){
               //this for resolution Dekstop-up
            this.ResizeScreen = 3
            }
         },

         getWindowHeight(event){
            this.windowHeigt = document.documentElement.clientHeight;
         },

         TrigDispatch(){
            this.$store.dispatch('GET_STUDY_DASHBOARD')
            this.$store.dispatch('GET_WEEKLY_PROGRESS')
            this.$store.dispatch('POST_B2C_SUMMARY')
            this.$store.dispatch("GET_PROFILE_SESSION_HISTORY")
            this.$store.dispatch('GET_STUDY_DASHBOARD_COACHING')
         }
      },
      
      computed: {
         GET_AdditionaLinfo(){
			   return this.$store.state.DataAdditional
         },
         GET_SessionHistory(){
            return this.$store.state.SessionHistory
         },
         DataSession() {
            const items = []
            const dataValue = this.GET_SessionHistory.new_appointment

            dataValue.forEach(function (value, key) {
               moment(value.date).format('MMMM, Do YYYY')
               items.push(dataValue);
               // console.log(key +" " + value.date+ " " + value.start_time);
            });

            function compare(a, b){
               if (a.date > b.date) return -1;
               if (a.date < b.date) return 1;
               if (a.start_time < b.start_time) return -1;
               if (a.start_time > b.start_time) return 1;
            }

            return [...dataValue].sort(compare)
         },
         CheckLoad(){
            if (this.$store.getters.CCK_STUDENT_PROFILE !== 'IS NULL'){
               const ItemsCont = []
               for (var i in this.$store.getters.CCK_STUDENT_PROFILE){
                  ItemsCont.push(this.$store.getters.CCK_STUDENT_PROFILE[i])
               }
               return ItemsCont
            }else{
               return 'IS NULL'
            }
         }
      },
      watch: {
          CheckLoad(val){
            if (val === 'IS NULL'){
               this.TrigDispatch()
               setTimeout(() => {
                  if (val === 'IS NULL'){
                     this.$store.commit('MuttAllertCont', {
                        Alert : 'yes',
                        TextAlert : 'Something wrong, Please Reload your Browser'
                     })
                     this.myData = null
                     console.log(val)
                  }
               }, 1000);
               
            }else{
               const DataSessionHis = val[0]
               const DataCard = val[1]
               // const DataStudy = val[2]

               if (DataSessionHis == 200 && DataCard == 200){
                  this.myData = 'Success'
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'no',
                     TextAlert : 'Success'
                  })
               }else if(DataSessionHis == 400 || DataCard == 400){
                  this.myData = null
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'yes',
                     TextAlert : 'Bad Request, Please try again in a few minutes'
                  })
               }else if(DataSessionHis == 500 || DataCard == 500){
                  this.myData = null
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'yes',
                     TextAlert : 'Server Internal Error, Please try again in a few minutes'
                  })
               }
            }
         }
      },

      beforeDestroy() {
         window.removeEventListener('resize', this.getWindowWidth);
         window.removeEventListener('resize', this.getWindowHeight);
      },

       destroyed() {
         this.$store.commit('MuttAllertCont', {
            Alert : 'no',
            TextAlert : 'exit'
         })
      },
     
   }
</script>

<style lang="scss">
   @import '../../styles/StudentProfileView.scss';
</style>



