<template>
   <div class="BXviewcoach__BXInfoStudent__BxCoachNote">
      <div class="bxNoteTitle">
         <p>Coach Notes</p>
         <span>Last Sessions</span>
         <hr>
      </div>
      <div class="BxNoteCard">
         <div class="bxCard" v-for="(n, index) in this.DataSession" :key="index">
            <div class="bxCard__bxHeader">
               <div class="bxCard__bxHeader__BxAvatar">
                  <v-avatar>
                     <img
                        :src="n.avatar"
                        alt="avatar"
                     >
                  </v-avatar>
                  <div class="BxName">
                     <p>{{n.coach_name}}</p>
                     <p>Last Sessions Notes</p>
                  </div>
                  
               </div>
               <div class="bxCard__bxHeader__BxDateLastSession">
                  <p>{{n.date | dateFormat}}</p>
                  <p>{{n.start_time}}</p>
               </div>
            </div>

            <div class="bxCard__BxNOTE">
              {{n.coach_notes}}
            </div>
         </div>
      </div>
   </div>
</template>

<script>
   import moment from 'moment'
   export default {
      props:[
         'DataSession'
      ],
      filters: {
         dateFormat: function (value) {
            if (value) {
               return moment(String(value)).format('ddd, MMMM  D  YYYY')
            }
         }, 
      }
   }
</script>



